<template>
  <div class="Test22">
    <button class="button1" @click="lookAnswer">提交答案</button>
    <button class="button" @click="changeTest23Page">下一题</button>
    <br><br>
    <div class="pro2">
      <div class="pro2-1">
        <input type="radio" name="answer" value="F"/><span class="word">26&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</span>
        <input type="radio" name="answer" value="T" :checked="choice"/><span class="word" :class="tStyle">2600&emsp;&emsp;&emsp;&emsp;&emsp;&emsp;</span>
        <input type="radio" name="answer" value="F"/><span class="word">26000&emsp;&emsp;&emsp;&emsp;&emsp;</span>
        <input type="radio" name="answer" value="F"/><span class="word">260</span>
      </div>
      <div class="countTime">
        <p>倒计时：{{ count }}</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "test22",
  data() {
    return {
      count: 20, // 初始值为20秒
      choice:false,
      tStyle:''
    };
  },
  mounted() {
    // 启动计时器，每秒更新count值
    this.timer = setInterval(() => {
      if (this.count > 0) {
        this.count--;
      } else {
        this.lookAnswer()
        clearInterval(this.timer); // 倒计时结束，停止计时器
      }
    }, 1000);
  },
  beforeDestroy() {
    // 组件销毁时清除计时器
    clearInterval(this.timer);
  },
  methods:{
    changeTest23Page(){
      this.$router.push({path:'/test23'})
    },
    lookAnswer(){
      var answer=document.getElementsByName("answer");
      if(answer[1].checked){
        alert("恭喜你，回答正确！")
      } else {
        alert("回答错误！")
      }
      this.choice = true
      this.tStyle = "T"
    },
  }
}
</script>

